import React, { Component } from 'react';
import { Col, Row, Input, Modal, ModalBody} from 'reactstrap';
//import { toast } from 'react-toastify';
import MUIDataTable from "mui-datatables";
//import  { Link } from 'react-router-dom';
import 'react-toastify/dist/ReactToastify.css';
//import commonService from '../../../core/services/commonService';
//import AutoCompletePlaces from '../../../core/google-map/AutoCompletePlaces';
//import Loader from '../../Loader/Loader';
//import UsersData from './UsersData';


import './UnApprovedUser.css'
const popupImgUrl = `${process.env.PUBLIC_URL}/images/popup-img.png`;
class UnApprovedUser extends Component {
  constructor(props){
    super(props);
    this.state = {
      loading: true,
      userList: [],
      modal: false, 
      rowIndex: -1,
      formProccessing: false,
      formField: {paitent_name: '', email: '', first_name: '', last_name: '', skype_id: '', latitude: '', longitude: '',  phoneNumber: '', address: '', city: '', state: '', country: '', postalCode: '', role: '', status: '' },
      errors: {paitent_name: '', email: '', contact_person: '', role: '', address: '', error: ''},
      formValid: true,
      items: [],
      filterItem: { filter_paitent_id: '', country: '', state: '', custom_search: '', filter_accept_trial: ''},
      categoryList: [],
      templateList: [],
      deepProfileTemplateList: []
    } 

        
  }
  componentDidMount() { 
    
  }

  toggleModal = () => {
    this.setState({
      modal: !this.state.modal,      
    });
  }
 

  render() {
    let rowsItem = [/*{
      patientDetails: "Jhon Smith",
      consentForm: "Download Form",
      deepProfileForm: "Download Form",
      suggestAnotherTrial: "",
      suggestNewTrialForm: "",
      action: ""
    },{
      patientDetails: "Jhon Smith",
      consentForm: "Download Form",
      deepProfileForm: "Download Form",
      suggestAnotherTrial: "",
      suggestNewTrialForm: "",
      action: ""
    },{
      patientDetails: "Jhon Smith",
      consentForm: "Download Form",
      deepProfileForm: "Download Form",
      suggestAnotherTrial: "",
      suggestNewTrialForm: "",
      action: ""
    },{
      patientDetails: "Jhon Smith",
      consentForm: "Download Form",
      deepProfileForm: "Download Form",
      suggestAnotherTrial: "",
      suggestNewTrialForm: "",
      action: ""
    }*/];
    const columns = [      
      {
        label: 'Patient Details',
        name: 'patientDetails',
      },
      
      {
        label: 'Consent Form',
        name: 'consentForm',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
           
            return (
              <p>Download Form <i className="fa fa-download"></i></p>
            );
          },
        }
      },
      {
        label: 'Deep Profile Form',
        name: 'deepProfileForm',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
           
            return (
              <p>Download Form <i className="fa fa-download"></i></p>
            );
          },
        }
      },
      {
        label: 'Suggest another trial',
        name: 'suggestAnotherTrial',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
           
            return (
              <Input name="suggest_another_trial" />
            );
          },
        }
      },
       {
        label: 'suggest new trial form',
        name: 'suggestNewTrialForm',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
           
            return (
              <Input name="suggest_new_trial_form" type ="select" >
                <option value="">Select</option>
                <option value="Form 1">Form 1</option>
                <option value="Form 2">Form 2</option>
              </Input>
            );
          },
        }
      },
      {
        label: 'Action',
        name: 'action',
        options: {
          filter: true,
          customBodyRender: (value, tableMeta, updateValue) => {
           
            return (
             <p><button title="Edit " className="btn-edit" ><i className="fa fa-check"></i> </button>
             <button title="Delete " className="btn-close-D" onClick = {() => {this.setState({modal: true})}}><i className="fa fa-times"></i> </button>
          </p>
            );
          },
        }
      },
    ];
    const options = {
      search: false,viewColumns: false,
      filter: false,
      searchOpen: false,
      print: false,
      download: false,
      responsive: 'stacked',
      selectableRows: 'none',
      textLabels: {
        body: {
          noMatch: this.props.dataTableLoadingStatus ? "Processing........" : "",
          toolTip: "Sort",
          columnHeaderTooltip: column => `Sort for ${column.label}`
        },
      },
      fixedHeaderOptions: { xAxis: false, yAxis: false }

    };

    return (
      <div className="animated fadeIn header-Table">
        <Row>
          <Col lg={2}>
            <div className="btn btn-back-Table" onClick= {() => {this.props.history.goBack()}}>Back</div>
          </Col>

          <Col lg={8}>
            <p className="Patient-text">New Patient Approval</p>
          </Col>

          {/*<Col lg={8} className="text-right">
            <div className="btn btn-add-patient">Add New Patient</div>            
          </Col>*/}
        </Row>
        <MUIDataTable
        
        data={rowsItem}
        columns={columns}
        options={options}
      />
      
      <Modal isOpen={this.state.modal} toggle={this.toggleModal} className="Popup-Modal-YesNo full-width-modal-section organization-modal">
          <ModalBody className="">
            <Row>
              <Col md = {"12"}>
              <div className="popup-head-img">
               <img src= {popupImgUrl} className="imghead" alt ="img" width = "100px" height = "100px"/>
              </div>
              <div className="popup-C">              
                <h2>Are you sure to delete patient</h2>
               
                <button className="btn addBtn btn-no">No</button>
                <button className="btn addBtn btn-yes">Yes</button>
              </div>
              </Col>
            </Row>           
          </ModalBody>
      </Modal>
      
      </div>
      
    )
  }
}


export default UnApprovedUser;
